import React from 'react'
import pic1 from '../imgs/logo.jpg'
import {Link} from 'react-scroll';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
const Navbar = () => {
  return (
    <nav className='navbar navbar-expand-lg navbar-light bg-dark fixed-top'>
      <div className='container my-3'>
        <a className='navbar-brand' href='#' style={{color:'var(--primary-white-color)'}}>
          STEVEN
        </a>
        <button
          className='navbar-toggler'
          type='button'
          data-toggle='collapse'
          data-target='#navbarSupportedContent'
          aria-controls='navbarSupportedContent'
          aria-expanded='false'
          aria-label='Toggle navigation'
        >
          <FontAwesomeIcon icon={faBars} style={{color:'#fff'}}/>
        </button>

        <div className='collapse navbar-collapse' id='navbarSupportedContent'>
          <ul className='navbar-nav ml-auto'>
            <li className='nav-item active'>
              <Link smooth={true} to='home' offset={-90} className='nav-link' href='#'>
                Home <span className='sr-only'>(current)</span>
              </Link>
            </li>
            <li className='nav-item'>
              <Link smooth={true} to='services' offset={-90} className='nav-link' href='#'>
                services
              </Link>
            </li>
            <li className='nav-item'>
              <Link smooth={true} to='experience' offset={-90} className='nav-link' href='#'>
                experience
              </Link>
            </li>
            <li className='nav-item'>
              <Link smooth={true} to='portfolio' offset={-90} className='nav-link' href='#'>
                portfolio
              </Link>
            </li>
            <li className='nav-item'>
              <Link smooth={true} to='contacts' offset={-90} className='nav-link' href='#'>
                contacts
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

export default Navbar
